import React from 'react';
import style from './checkbox.module.scss';
interface Props {
  label?: string;
  id?: string;
  name?: string;
  handleChange?: (e: React.ChangeEvent<HTMLInputElement>) => void;
  checked?: any;
  inputRef?: any;
  labelClass?: any;
  checkboxClass?: any;
  error?: boolean;
}

const Checkbox = ({
  id,
  name,
  label,
  error,
  inputRef,
  labelClass,
  checkboxClass,
  handleChange,
  checked,
}: Props) => {
  return (
    <div>
      <label
        className={`${style.container} ${labelClass}`}
        style={{ color: error ? '#ff5050' : '' }}
        htmlFor={id}
      >
        {label}
        <input
          type="checkbox"
          name={name}
          ref={inputRef}
          id={id}
          onChange={handleChange}
          checked={checked}
        />
        <span
          className={`${style.checkMark} ${checkboxClass}`}
          style={{ borderColor: error ? '#ff5050' : '' }}
        ></span>
      </label>
    </div>
  );
};

export default Checkbox;
